.info{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 50px;
}

.info-item{
    display: flex;
    width: 120px;
    flex-direction: column;
    align-items: center;
    border-radius: 5px;
    padding: 15px 0;
    cursor: pointer;
}

.info-item:hover{
    background-color: rgba(0, 0, 0, 0.05);
}

.info-item__name{
    font-size: 35pt;
    font-weight: bold;
    
    display: flex;
    align-items: flex-end;
}

.info-item__data{
    font-size: 20pt;
    font-weight: lighter;

    margin: 15px 0 15px 0;
}

.info-item__unit{
    font-size: 20pt;
    font-weight: normal;
}

.info-item__name__index{
    font-size: 20pt;
    font-weight: normal;
}