.page{
    position: relative;

    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-height: 100vh;

    overflow-y: auto;

    transition: margin 0.3s ease;
}

.page-model{
    width: 83vw;
}

.page-model .info, .page-model .model{
    margin-left: 17vw;
}

.page-graph{
    width: 100vw;
    transition: background 0.4s ease-out;
    
    padding-top: 150px;

    justify-content: flex-start;
}

.page:not(.active){
    overflow: hidden;
    cursor: pointer;
}

.page:not(.active):hover{
    background-color: rgba(0, 0, 0, 0.2);
}

.page-name{
    position: absolute;
    top: 50px;
    left: 50px;

    font-size: 4rem;
    font-weight: bold;
    /* transition: color 500ms cubic-bezier(0.495, 0.620, 0.355, 1.245); */
}

.page-back{
    position: fixed;
    top: 0;
    left: 0;
    width: 10vw;
    height: 100%;
    transition: background 0.4s ease-out;
    cursor: pointer;
}

.page-graph.active .page-back:hover{
    background-color: rgba(255, 255, 255, 0.1);
}

.page-graph__time-slider{
    position: static;
    color: #fff;
}

.page-graph__time-slider .rangeslider__fill{
    background-color: #bdc3c7;
}

.page-graph__time-slider .rangeslider-horizontal{
    background-color: #323232;
    box-shadow: none;
}

.page-graph__time-slider .rangeslider__handle{
    border: none;
    background-color: #fff;
}

.page-graph__time-slider .rangeslider-horizontal{
    width: 50vw;
}