@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500;700&display=swap);
*{
    margin: 0;
    padding: 0;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;       
}

body{
    background-color: #fedc32;
    font-family: 'Montserrat', sans-serif;
}
.App{
    display: flex;
    overflow: hidden;
    height: 100vh;
    transition: background-color 0.5s ease-in-out;
}

::-webkit-scrollbar {
    width: 0;
}
.model{
    --svg-offset: 200px;

    width: calc(466px + var(--svg-offset));
    min-height: calc(195px + var(--svg-offset));

    display: flex;
    justify-content: center;
    align-items: center;

    margin-top: var(--svg-offset);

    position: relative;
}

svg{
    width: 466px;
    height: 195px;
}

.model-resist{
    width: 28px;
    height: 120px;
    border: 3px solid black;
    border-radius: 5px;
}

.model-capacity{
    display: flex;

}

.model-capacity__item{
    width: 5px;
    height: 85px;
    background-color: #000;
}

.model-capacity__item:nth-child(1){
    margin-right: 5px;
}

.model-inductance{
    width: 200px;
    height: 37.5px;
    overflow: hidden;

    display: flex;
    flex-direction: row;
}

.model-inductance__item{
    width: 75px;
    height: 75px;
    border-radius: 100%;
    border: 8px solid #000;
}

#path{
   stroke-width: 50;
}

.model-resistor{
    width: 25px;
    height: 100px;
    background-color: #fff;
    border: 3px solid #000;
    left: 87px;
    position: absolute;
}

.model-capacity{
    width: 25px;
    height: 56px;
    display: flex;
    position: absolute;
    top: 75px;
    justify-content: space-between;
    background-color: #fedc32;
}

.model-capacity__item{
    width: 10px;
    height: 100%;
    background-color: #000;
}

.model-eds{
    width: 50px;
    height: 50px;
    position: absolute;
    background-color: #fff;
    border: 3px solid #000;
    border-radius: 100px;

    line-height: 50px;
    text-align: center;
    font-size: 32px;
    right: 75px;
}
.data-input{
    font-size: 24pt;
    position: absolute;
}

.data-input.vertical{
    display: flex;
    flex-direction: row;
}   

.data-input.vertical .data-input__input__value{
    -webkit-writing-mode: vertical-rl;
        -ms-writing-mode: tb-rl;
            writing-mode: vertical-rl;
    -webkit-text-orientation: mixed;
            text-orientation: mixed;
    text-align: center;
    transform: rotate(180deg);
}

.data-input__input{
    display: flex;
    justify-content: center;
}

.data-input__input__value{   
    font-size: 24pt;
    margin-right: 10px;
}

.data-input .rangeslider-horizontal{
    margin-top: 15px;

    width: 200px;
    height: 7px;
    border-radius: 8px;
    background-color: #000;
}

.data-input .rangeslider-vertical{
    margin-right: 15px;

    width: 7px;
    height: 200px;
    border-radius: 8px;
    background-color: #000;
}

.data-input .rangeslider__fill{
    background-color: #000;
}

.data-input .rangeslider__handle{
    background-color: #fff;

    width: 13px;
    height: 13px;

    outline: none;

    box-shadow: none;
    border: 2px solid #000;
}

.data-input.vertical .rangeslider__handle{
    transform: translate(25%, 0);

    border-radius: 100%;
}

.data-input .rangeslider__handle::after{
    content: none;
}
.eds-graph{
    width: 60vw;
    min-height: 300px;
    margin-bottom: 50px;
    overflow: hidden;
    position: relative;
}

.eds-graph-axis{
    position: absolute;
    color: #fff;
}
.page{
    position: relative;

    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-height: 100vh;

    overflow-y: auto;

    transition: margin 0.3s ease;
}

.page-model{
    width: 83vw;
}

.page-model .info, .page-model .model{
    margin-left: 17vw;
}

.page-graph{
    width: 100vw;
    transition: background 0.4s ease-out;
    
    padding-top: 150px;

    justify-content: flex-start;
}

.page:not(.active){
    overflow: hidden;
    cursor: pointer;
}

.page:not(.active):hover{
    background-color: rgba(0, 0, 0, 0.2);
}

.page-name{
    position: absolute;
    top: 50px;
    left: 50px;

    font-size: 4rem;
    font-weight: bold;
    /* transition: color 500ms cubic-bezier(0.495, 0.620, 0.355, 1.245); */
}

.page-back{
    position: fixed;
    top: 0;
    left: 0;
    width: 10vw;
    height: 100%;
    transition: background 0.4s ease-out;
    cursor: pointer;
}

.page-graph.active .page-back:hover{
    background-color: rgba(255, 255, 255, 0.1);
}

.page-graph__time-slider{
    position: static;
    color: #fff;
}

.page-graph__time-slider .rangeslider__fill{
    background-color: #bdc3c7;
}

.page-graph__time-slider .rangeslider-horizontal{
    background-color: #323232;
    box-shadow: none;
}

.page-graph__time-slider .rangeslider__handle{
    border: none;
    background-color: #fff;
}

.page-graph__time-slider .rangeslider-horizontal{
    width: 50vw;
}
.info{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 50px;
}

.info-item{
    display: flex;
    width: 120px;
    flex-direction: column;
    align-items: center;
    border-radius: 5px;
    padding: 15px 0;
    cursor: pointer;
}

.info-item:hover{
    background-color: rgba(0, 0, 0, 0.05);
}

.info-item__name{
    font-size: 35pt;
    font-weight: bold;
    
    display: flex;
    align-items: flex-end;
}

.info-item__data{
    font-size: 20pt;
    font-weight: lighter;

    margin: 15px 0 15px 0;
}

.info-item__unit{
    font-size: 20pt;
    font-weight: normal;
}

.info-item__name__index{
    font-size: 20pt;
    font-weight: normal;
}
